import React from 'react'
import { state } from '../../Data/HomeData/HomeData'
import "./Home.css";
function HForm() {
  return (
    <div  className="hform bg-secondary  col-md-3 mt-3 p-4 offset-md-2 rounded ">
    <h3 className="text-center text-white">Enter Details</h3>
    <form action="">
      <input type="text"  className="w-100 py-2 my-3" placeholder="Enter Name of Student" />
      <input type="tel"  className="w-100 py-2 my-3" placeholder="Enter Mobile Number" />
      <input type="email"  className="w-100 py-2 my-3" placeholder="Enter Email Address" />
      <select  className="form-select py-2 my-3"   aria-label="Default select">
        <option defaultValue>State</option>
        {state.map((data,idx)=>(
        <option key={idx} value={`${data.s}`}>{data.s}</option>
        ))}
      </select>
      <button className="btn btn-warning w-100 py-2 my-3"><h5>Schedule Free Class </h5></button>
    </form>
  </div>
  )
}

export default HForm
