import React from 'react'
import { Link } from 'react-router-dom'
import "./Plan.css"
function PlanCard(props) {
  return (
    <>
     <div className="batch-card " >
      <div className="box card p-5  pb-1" style={{backgroundColor:props.color}}>
        <h3 className="text-center  text-capitalize mb-1 "> {props.type}</h3>
        <ol className="text-start plan-list ps-0 text-capitalize">
        <li className='my-4'>{props.live}</li>
        <li className='my-4'>{props.exercise}</li>
        <li className='my-4'>{props.record}</li>
        <li className='my-4'>{props.time}</li>
        <li className='my-4'>Rs.{props.fees}/Month</li>
       <li> <Link to="/tutorweb/learn" className='btn w-100 trial btn-warning'>{props.learn}</Link></li>
       <li  className='my-4 d-flex'> <button className='btn w-100 trial mx-3 btn-primary'>{props.contact}</button>
       <button className='btn w-100 trial btn-outline-success'>{props.subscribe}</button></li>
        </ol>
      </div>
    </div>
      
    </>
  )
}

export default PlanCard
