import React from 'react'
import { PlanData } from '../../Data/PlanData/Plan'
import PlanCard from './PlanCard'

function Plan() {
  return (
   <div className='conatiner-fluid my-4  '>
    <div className="row my-5 mb-5">
        <div className="col-12 rounded p-3 m-auto">
            
    <h1
        className="text-center text-uppercase py-5 m-0"
      >
       Study Plans 
      </h1>
  
    <div className="row mt-1  row-cols-sm-2 row-cols-md-3 ">
      {PlanData.map((data,pkey)=>(
                <div key={pkey} className="mt-4 p-5">    
                 <PlanCard
                 color={data.color}
                 type={data.type}
                 live={data.live}
                 exercise={data.exercise}
                 record={data.record}
                 time={data.time}
                 fees={data.fees}
                 contact={data.contact}
                 subscribe={data.subscribe}
                 learn={data.learn}
                 />
                 </div>
                 ))}
                
                 
        </div>
    </div>
    </div>
   </div>
  )
}

export default Plan
