import axios from "axios";
import React from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
function CForm() {
    const [name,setname]=useState("");
    const [email,setemail]=useState("");
    const [phone,setphone]=useState("");
    const [country,setcountry]=useState("");
    const [city,setcity]=useState("");
    const [about,setabout]=useState("");
    const [agree,setagree]=useState(false);
    const submitform=(e)=>{
        e.preventDefault();
        const obj={
            name:name,
            email:email,
            phone:phone,
            country:country,
            city: city,
            about: about,
        }
        axios.post("http://localhost:3001/contact",{
            name:name,
            email:email,
            phone:phone,
            country:country,
            city: city,
            about: about,
        }).then((err,res)=>{
            if(err){
                console.log(err)
            }else{
        console.log("success")
            }
        })
        setname("");
        setemail("");
        setphone("");
        setcountry("");
        setcity("");
        setabout("");
        console.log(obj)
    }

  return (
    <div>
      <form className="text-capitalize " action="subimt">
        <div className="flex-column d-flex mt-3">
          <label htmlFor="name">
            Your Name <span className="text-danger">*</span>
          </label>
          <input required value={name} onChange={(e)=>{setname(e.target.value)}}
            className="text-capitalize bg-light"
            type="text"
            placeholder="e.g Your full name "
          />
        </div>
        <div className="flex-column d-flex mt-3">
          <label htmlFor="name">
            Your Email id<span className="text-danger">*</span>{" "}
          </label>
          <input value={email} onChange={(e)=>{setemail(e.target.value)}}
            className="text-capitalize bg-light"
            type="email"
            placeholder="e.g Your email id "
          />
        </div>
        <div className="flex-column d-flex mt-3">
          <label htmlFor="name">
            Your phone<span className="text-danger">*</span>{" "}
          </label>
          <input value={phone} onChange={(e)=>{setphone(e.target.value)}}
            className="text-capitalize bg-light"
            type="text"
            placeholder="e.g Your Contact +00000000000 "
          />
        </div>
        <div className="flex-column d-flex mt-3">
          <label htmlFor="name">
            Country Name<span className="text-danger">*</span>{" "}
          </label>
          <input value={country} onChange={(e)=>{setcountry(e.target.value)}}
            className="text-capitalize bg-light"
            type="text"
            placeholder="e.g Your Country Name"
          />
        </div>
        <div className="flex-column d-flex mt-3">
          <label htmlFor="name">
            Country city <span className="text-danger">*</span>{" "}
          </label>
          <input value={city} onChange={(e)=>{setcity(e.target.value)}}
            className="text-capitalize bg-light"
            type="text"
            placeholder="e.g Your city name"
          />
        </div>
        <div className="flex-column d-flex mt-3">
          <label htmlFor="name">
            Your Message <span className="text-danger">*</span>
          </label>
          <textarea value={about} onChange={(e)=>{setabout(e.target.value)}}
            className="bg-light rounded"
            name="message"
            id=""
            cols="30"
            rows="4"
            placeholder="e.g You are intrested to get the services for yourself . for your kids . or anyone else . Kindly write name age and Gender of the student(s) .intrested for classes."
          ></textarea>
        </div>
        <div class="form-check mt-4">
  <input onChange={(e)=>{setagree(true)}}
   className="form-check-input" type="checkbox" value={agree} id="flexCheckDefault"/>
  <label className="form-check-label" for="flexCheckDefault">
    Agree<span className="text-danger">*</span>
  </label>
</div>

        <div className="declaration  row">
                  <p>
                    <i className="uil uil-angle-right mb-0"></i>By submiting your
                    conatact are agreed to the
                    <Link to="/tutorweb/contact/privacy"> terms of services and privacy policy </Link> .
                    Rest assured that we keep your personal data safe do not
                    sell or redistribute to anyone
                  </p>
                  <button type="submit" onClick={submitform} className="btn col-3 m-auto trial btn-primary">
                    Submit
                  </button>
                </div>
      </form>
    </div>
  );
}

export default CForm;
