import React from 'react'
import { about } from '../../Data/AboutData/AboutData'
import Footer from '../footer/Footer'
import SubAbout from './SubAbout'
function About() {
  return (
    <div>
        {about.map((data,key)=>(
      <SubAbout
     class={data.class}
     head={data.head}
     left={data.left}
     right={data.right}
     key={key}
      />
      ))}
      <div className='misssio  p-5' style={{backgroundColor:"rgb(118 118 109 / 26%)"}}>
        <div className='m-5'>
        <h1 className='text-center my-4'>Our Mission</h1>
        <p style={{fontSize:"40px"}} className="col-8 m-auto">Our mission is to empower Students with the tools
they need to achieve dynamic learning
environments that engage and inspire students.</p>
<h1 className='text-center my-4'>Our Vision</h1>
<p style={{fontSize:"40px"}} className="col-8 m-auto">We believe in the power of education to transform
lives and empower communities. Our Vision is to
provide affordable, convenient, and access to highquality education to everyone.
</p>
</div>
      </div>
      <Footer/>
    </div>
  )
}

export default About
